import { Component } from '@angular/core';
import { AlertController, NavController, NavParams } from 'ionic-angular';

import { RegisterAuthPage } from '../register-auth/register-auth';
import { TermPage } from '../term/term';

// common 
import { AppmgrProvider } from '../../providers/appmgr/appmgr';
import { HttpProvider } from '../../providers/http/http';
import { CommonProvider } from '../../providers/common/common';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  agreeTerm = false;
  mem_type = 0;
  email = '';
  password = '';
  repassword = '';

  validateFlag = false;
  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController,
    private appmgr: AppmgrProvider,
    public common: CommonProvider,
    public http: HttpProvider
  ) {
    console.log("register come");
  }

  ionViewWillEnter() {
    let saveInfo = this.appmgr.getSaveInfo();
    this.mem_type = saveInfo.mem_type;
    
    this.agreeTerm = false;
    this.mem_type = 0;
    this.email = '';
    this.password = '';
    this.repassword = '';
    this.validateFlag = false;
  }

  back() {
    this.navCtrl.pop({animate:false});
  }
  /////////
  checkEmail() {
    
    let sendData = [];
        sendData["email"] = this.email;
        sendData["password"] = '';
    this.http.postHttpData("/checkUser", sendData, (result) => {

      if(result) {
        this.validateFlag = true;
        let str = '  .';
        this.confirmAlert(str);
      }else {
        this.validateFlag = false;
      }
    });
  }
  createUser() {
    // 
    
    if( !this.validate() ) {
      return false;
    }
    
    let sendData = [];
        sendData["email"] = this.email;
    this.http.postHttpData("/sendAuthKey", sendData, (result) => 
    {
      console.log(result);
      if(result) {
        this.navCtrl.push(RegisterAuthPage, { email: this.email, password: this.password, authKey: result }, {animate:false} );
      }
    });

  }
  /////////
  personal() {
    this.navCtrl.push(TermPage, { type:"personal","email":this.email,"pass":this.password,"repass":this.repassword },{animate:false} ).then(()=>{
      this.navCtrl.getActive().onDidDismiss(data => {
        console.log(data);
        if(data.email!=""){

          this.email=data.email;
        }
        if(data.pass!=""){
          this.password=data.pass;  
        }
        if(data.repass!=""){
        
          this.repassword=data.repass;
        }
      });
    })
  }
  term(){
    this.navCtrl.push(TermPage, { type:"term" ,"email":this.email,"pass":this.password,"repass":this.repassword},{animate:false} ).then(()=>{
      this.navCtrl.getActive().onDidDismiss(data => {
        console.log(data);
        if(data.email!=""){

          this.email=data.email;
        }
        if(data.pass!=""){
          this.password=data.pass;  
        }
        if(data.repass!=""){
        
          this.repassword=data.repass;
        }
      });
    })
  }
  
  validate () {
    let str = '';
    if( this.email == '' ) {
      str = ' .';
      this.confirmAlert(str);
      return false;
    }
    else if(this.email.indexOf('@') == -1) {
      str = '   .';
      this.confirmAlert(str);
      return false;
    }
    // 
    if( this.validateFlag ) {
      str = '  .';
      this.confirmAlert(str);
      return false;
    }
    
    // password
    if( this.password == '' ) {
      str = ' .';
      this.confirmAlert(str);
      return false;
    }
    else if( this.password != this.repassword ) {
      str = '  .';
      this.confirmAlert(str);
      return false;
    }
    else if( this.password.length < 8 ) {
      str = '   .';
      this.confirmAlert(str);
      return false;
    }
    // term
    if( this.agreeTerm == false ) {
      str = '    <br/ > .';
      this.confirmAlert(str);
      return false;
    }
    return true;
  }

  confirmAlert(str) {
    let alert = this.alertCtrl.create({      
        subTitle: str,
        buttons: ['']
    });
    alert.present({animate:false});
  }
}



